import React from 'react';
import Converter from '../converter'
import { connect } from 'react-redux';
import { setAllCurrencyObj } from '../../store/actions/converter';
import { setCurrencyValues } from '../../store/actions/converter';
import { setCurrencyNamesAndValues } from '../../store/actions/converter';

class ConverterContainer extends React.Component {
  preRenderFunction = () => {
    const { setAllCurrencyObj } = this.props;
    const { setCurrencyNamesAndValues } = this.props;
    const { urlAPI } = this.props;
    const { fromCurrencyValue, toCurrencyValue } = this.props;
    fetch(urlAPI)
      .then(response => response.json())
      .then(data => {
        let dataValuteKeys = Object.keys(data.Valute).sort();
        let currencyObj = {RUB: {Value: 1, Nominal: 1}};
        dataValuteKeys.forEach(v => {
          let { CharCode, Nominal, Value } = data.Valute[v];
          currencyObj[CharCode] = {
            Nominal,
            Value,
          };
        });
        setCurrencyNamesAndValues(dataValuteKeys[0], dataValuteKeys[1], fromCurrencyValue, toCurrencyValue);
        setAllCurrencyObj(currencyObj);
      });
  }

  convert = (currencyValue, fromCurrencyName, toCurrencyName, allCurrencyObj) => {
    if(currencyValue === '') return '';
    let currencyObjFrom = allCurrencyObj[fromCurrencyName];
    let currencyObjTo = allCurrencyObj[toCurrencyName];
    currencyValue = currencyValue.split(/ /).join('').split(/,/).join('.');
    return Math.ceil(
      currencyValue *
      currencyObjFrom.Value /
      currencyObjFrom.Nominal *
      currencyObjTo.Nominal /
      currencyObjTo.Value * 100) / 100 + '';
  }

  castToNumber = str => str.split(/ /).join('').split(/,/).join('.');

  onFromInputChange = e => {
    const { setCurrencyValues } = this.props;
    const { fromCurrencyName, toCurrencyName, allCurrencyObj } = this.props;
    const newFromCurrencyValue = this.castToNumber(e.target.value);
    const newToCurrencyValue = this.convert(e.target.value, fromCurrencyName, toCurrencyName, allCurrencyObj);
    setCurrencyValues(newFromCurrencyValue, newToCurrencyValue)
  }

  onToInputChange = e => {
    const { setCurrencyValues } = this.props;
    const { fromCurrencyName, toCurrencyName, allCurrencyObj } = this.props;
    const newFromCurrencyValue = this.convert(e.target.value, toCurrencyName, fromCurrencyName, allCurrencyObj);
    const newToCurrencyValue = this.castToNumber(e.target.value)
    setCurrencyValues(newFromCurrencyValue, newToCurrencyValue)
  }

  onFromDropListChange = (e) => {
    const { setCurrencyNamesAndValues } = this.props;
    const { fromCurrencyName, toCurrencyName, allCurrencyObj } = this.props;
    const { fromCurrencyValue } = this.props;
    let newFromCurrencyName;
    let newToCurrencyName;
    if(e.target.innerHTML === toCurrencyName) {
      newFromCurrencyName = toCurrencyName;
      newToCurrencyName = fromCurrencyName;
    } else {
      newFromCurrencyName = e.target.innerHTML;
      newToCurrencyName = toCurrencyName;
    }
    const newFromCurrencyValue = fromCurrencyValue;
    const newToCurrencyValue = this.convert(fromCurrencyValue, newFromCurrencyName, newToCurrencyName, allCurrencyObj)
    setCurrencyNamesAndValues(newFromCurrencyName, newToCurrencyName, newFromCurrencyValue, newToCurrencyValue)
  }

  onToDropListChange = (e) => {
    const { setCurrencyNamesAndValues } = this.props;
    const { fromCurrencyName, toCurrencyName, allCurrencyObj } = this.props;
    const { fromCurrencyValue } = this.props;
    let newFromCurrencyName;
    let newToCurrencyName;
    if(e.target.innerHTML === fromCurrencyName) {
      newFromCurrencyName = toCurrencyName;
      newToCurrencyName = fromCurrencyName;
    } else {
      newFromCurrencyName = fromCurrencyName;
      newToCurrencyName = e.target.innerHTML;
    }
    const newFromCurrencyValue = fromCurrencyValue;
    const newToCurrencyValue = this.convert(fromCurrencyValue, newFromCurrencyName, newToCurrencyName, allCurrencyObj)
    setCurrencyNamesAndValues(newFromCurrencyName, newToCurrencyName, newFromCurrencyValue, newToCurrencyValue)
  }

  onRemoveTextButtonClick = (e) => {
    const { setCurrencyValues } = this.props;
    const newFromCurrencyValue = '';
    const newToCurrencyValue = '';
    setCurrencyValues(newFromCurrencyValue, newToCurrencyValue)
  }

  render() {
    const { fromCurrencyName, toCurrencyName } = this.props;
    const { fromCurrencyValue, toCurrencyValue } = this.props;
    const currencyList = Object.keys(this.props.allCurrencyObj).sort();
    return (
      <Converter
        currencyList={currencyList}
        preRenderFunction={this.preRenderFunction}
        fromCurrencyName={fromCurrencyName}
        toCurrencyName={toCurrencyName}
        fromCurrencyValue={fromCurrencyValue}
        toCurrencyValue={toCurrencyValue}
        onFromInputChange={this.onFromInputChange}
        onToInputChange={this.onToInputChange}
        onFromDropListChange={this.onFromDropListChange}
        onToDropListChange={this.onToDropListChange}
        onRemoveTextButtonClick={this.onRemoveTextButtonClick}
      />
    )
  }
}

const mapDispatchToProps = {
  setAllCurrencyObj,
  setCurrencyValues,
  setCurrencyNamesAndValues
};

const mapStateToProps = ({converter}) => {
  return {
    allCurrencyObj: converter.allCurrencyObj,
    fromCurrencyName: converter.fromCurrencyName,
    toCurrencyName: converter.toCurrencyName,
    fromCurrencyValue: converter.fromCurrencyValue,
    toCurrencyValue: converter.toCurrencyValue,
    urlAPI: 'https://www.cbr-xml-daily.ru/daily_json.js'
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ConverterContainer)