import { SET_ALL_CURRENCY_OBJ } from '../../actions/converter';
import { SET_CURRENCY_VALUES } from '../../actions/converter';
import { SET_CURRENCY_NAMES_AND_VALUES } from '../../actions/converter';

const initialState = {
  allCurrencyObj: {},
  fromCurrencyName: '',
  toCurrencyName: '',
  fromCurrencyValue: '',
  toCurrencyValue: '',
  urlAPI: 'https://www.cbr-xml-daily.ru/daily_json.js',
};

const converterReducer = (state = initialState, action) => {
  let allCurrencyObj;
  let fromCurrencyValue, toCurrencyValue;
  let fromCurrencyName, toCurrencyName;
  switch (action.type) {
    case SET_ALL_CURRENCY_OBJ:
      ({ allCurrencyObj } = action.payload);
      return {
        ...state,
        allCurrencyObj
      };
    case SET_CURRENCY_VALUES:
      ({ fromCurrencyValue, toCurrencyValue } = action.payload);
      return {
        ...state,
        fromCurrencyValue,
        toCurrencyValue
      };
    case SET_CURRENCY_NAMES_AND_VALUES:
      ({ fromCurrencyName, toCurrencyName } = action.payload);
      ({ fromCurrencyValue, toCurrencyValue } = action.payload);
      return {
        ...state,
        fromCurrencyName,
        toCurrencyName,
        fromCurrencyValue,
        toCurrencyValue
      };
    default:
      return state;
  }
};

export default converterReducer;