export const SET_ALL_CURRENCY_OBJ = 'SET_ALL_CURRENCY_OBJ';
export const SET_CURRENCY_VALUES = 'SET_CURRENCY_VALUES';
export const SET_CURRENCY_NAMES_AND_VALUES = 'SET_CURRENCY_NAMES_AND_VALUES';

export const setAllCurrencyObj = allCurrencyObj => {
  return {
    type: SET_ALL_CURRENCY_OBJ,
    payload: {
      allCurrencyObj
    }
  }
}

export const setCurrencyValues = (fromCurrencyValue, toCurrencyValue) => {
  return {
    type: SET_CURRENCY_VALUES,
    payload: {
      fromCurrencyValue,
      toCurrencyValue
    }
  }
}

export const setCurrencyNamesAndValues = (fromCurrencyName, toCurrencyName, fromCurrencyValue, toCurrencyValue) => {
  return {
    type: SET_CURRENCY_NAMES_AND_VALUES,
    payload: {
      fromCurrencyName,
      toCurrencyName,
      fromCurrencyValue,
      toCurrencyValue
    }
  }
}