import React from 'react';
import './style.css';

class Converter extends React.Component {
  validate = (pattern, str) => str.split('').filter(v => pattern.test(v)).join('');

  separateNumbers = str => {
    if (str.length > 1) {
      str = str.split('.');
      str[0] = str[0]
        .split('')
        .filter(v => v !== ' ')
        .reduce((a, b, i, arr) => i % 3 === arr.length % 3 ? a + ' ' + b : a + b);
      return str.join(',');
    } else return str;
  };

  inputLogic = input => {
    input.value = this.validate(/[0-9,]/, input.value);
  };

  componentDidMount() {
    const { preRenderFunction = () => undefined } = this.props;
    preRenderFunction();
  }

  render() {
    const { currencyList } = this.props;
    const { fromCurrencyName, toCurrencyName } = this.props;
    let { fromCurrencyValue, toCurrencyValue } = this.props;
    fromCurrencyValue = this.separateNumbers(fromCurrencyValue + '');
    toCurrencyValue = this.separateNumbers(toCurrencyValue + '');
    const removeButtonState = fromCurrencyValue.length > 0 ? 'visible' : 'hidden';
    const { onFromInputChange, onToInputChange } = this.props;
    const { onFromDropListChange, onToDropListChange } = this.props;
    const { onRemoveTextButtonClick } = this.props;
    return (
      <div className="converter">
        <div className="list-input">
          <div className="list-input__drop-list">
            <button className="list-input__drop-list__button">{fromCurrencyName}</button>
            <ul className="list-input__drop-list__list">
              {
                currencyList.map(v => {
                  return <li
                    key={v}
                    className="list-input__drop-list__list__item"
                    onMouseDown={(e) => onFromDropListChange(e)}>{v}</li>;
                })
              }
            </ul>
          </div>
          <div className="list-input__smart-input">
            <input
              className="list-input__smart-input__input"
              type="text"
              maxLength="20"
              placeholder="FROM"
              value={fromCurrencyValue}
              onChange={(e) => {
                this.inputLogic(e.target);
                onFromInputChange(e);
              }}/>
              <button
                className={`list-input__smart-input__button-remove-text ${removeButtonState}`}
                onClick={e => onRemoveTextButtonClick(e)}
              >X</button>
          </div>
        </div>
        <div className="list-input">
          <div className="list-input__drop-list">
            <button className="list-input__drop-list__button">{toCurrencyName}</button>
            <ul className="list-input__drop-list__list">
              {
                currencyList.map(v => {
                  return <li
                    key={v}
                    className="list-input__drop-list__list__item"
                    onMouseDown={(e) => onToDropListChange(e)}>{v}</li>;
                })
              }
            </ul>
          </div>
          <div className="list-input__smart-input">
            <input
              className="list-input__smart-input__input"
              type="text"
              maxLength="20"
              placeholder="TO"
              value={toCurrencyValue}
              onChange={(e) => {
                this.inputLogic(e.target);
                onToInputChange(e);
              }}/>
              <button
                className={`list-input__smart-input__button-remove-text ${removeButtonState}`}
                onClick={e => onRemoveTextButtonClick(e)}
              >X</button>
          </div>
        </div>
      </div>
    )
  }
}

export default Converter